import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import data from '../../data/dates.json';
import '../../styles/dates.scss';
import SearchBar from '../SearchBar'


// Dterminez si vous tes en dveloppement ou en production
const isDevelopment = process.env.NODE_ENV === 'development';

// Configurez le chemin d'image bas sur l'environnement
const imageUrl = isDevelopment
  ? '/quelle-date/'  // Chemin relatif pour le dveloppement
  : `${process.env.REACT_APP_IMAGE_PATH}`;  // Chemin complet pour la production

const DateComponent = () => {
  const [results, setResults] = useState([]);
  const [searchYear, setSearchYear] = useState('');
  const location = useLocation();

  useEffect(() => {
    // Rcupre le paramtre de recherche de l'URL
    const query = new URLSearchParams(location.search).get('search') || '';
    console.log("Query from URL:", query); // Ajout pour le dbogage
    setSearchYear(query); // Met  jour l'anne recherche

    // Filtre les donnes en fonction du paramtre de recherche
    const queryYear = parseInt(query, 10);
    console.log("Parsed queryYear:", queryYear); // Ajout pour le dbogage

    // Filtrer les rsultats pour ne garder que ceux de l'anne correspondant exactement  queryYear
    if (!isNaN(queryYear)) {
      const filteredResults = data.filter(event => event.annee === queryYear);
      console.log("Filtered results:", filteredResults); // Ajout pour le dbogage
      setResults(filteredResults);
    } else {
      setResults([]); // Si la query n'est pas un nombre, on vide les rsultats
    }
  }, [location.search]);

  return (

    <div> <div> <SearchBar /></div>
      {results.length > 0 ? (
        <div>
          {results.map((event, index) => (
            <div className="allResults" key={index}>
              <div className="yearList">
                <div>
                  <div className="eventLeft">
                    <div className="eventYearList">
                      <img src={`${imageUrl}/images/evenement.png`} alt="icne" />
                      <div className='eventTitle'>
                        <h3>L'vnement</h3>
                        <p>{event.evenement}</p>
                      </div>
                    </div>
                    {event.personnage && (
                      <div className="personnage">
                        <div className="personnagePhoto">
                          <img src={`${imageUrl}/${event.personnage.photoPersonnage}`} alt={event.personnage.name} />
                        </div>
                        <div className='personnageTxt'>
                          <h3 className="personnageName">{event.personnage.name}</h3>
                          <div className="personnageDescription">
                            {event.personnage.description}
                          </div>
                          <div className="mediaLinks">
                            <p>Pour les plus curieux !
                              <br />Dcouvrez ces lments d'un autre temps avec nos ressources d'aujourd'hui
                            </p>
                            {event.personnage.podcastUrl && (
                              <button onClick={() => window.open(event.personnage.podcastUrl, '_blank')}>
                                LE Podcast
                              </button>
                            )}
                            {event.personnage.youtubeUrl && (
                              <button onClick={() => window.open(event.personnage.youtubeUrl, '_blank')}>
                                LA Vido YouTube
                              </button>
                            )}
                          </div>
                        </div>
                      </div>
                    )}
                  </div>
                </div>
              </div>
              <div className="eventRight">
                <h2>L'anne {searchYear}</h2>
                <img className="banner" src={`${imageUrl}/${event.bannerImage}`} alt="Bannire thmatique" />
              </div>
            </div>
          ))}
        </div>
      ) : (
        <p> Application en cours de dveloppement. Aucun rsultat trouv pour l'anne {searchYear}.</p>
      )}
    </div>
  );
};

export default DateComponent;