import React, { useState } from 'react';
import { jsPDF } from 'jspdf';
import Select from 'react-select';
import eventsData from '../data/dates.json'
import '../styles/revision.scss'


const imgpdf = 'data:image/webp;base64,'


const years = Array.from(new Set(eventsData.map(event => event.annee))).sort((a, b) => a - b);
const yearOptions = years.map(year => ({ value: year, label: year.toString() }));

const PdfGenerator = () => {
    const [startYear, setStartYear] = useState(null);
    const [endYear, setEndYear] = useState(null);
    const [isCollapsed, setIsCollapsed] = useState(true);

    const handleGeneratePDF = () => {
        if (startYear === null || endYear === null) {
            alert('Veuillez slectionner une plage d\'annes.');
            return;
        }

        const filteredEvents = eventsData.filter(event =>
            event.annee >= startYear && event.annee <= endYear
        );

        if (filteredEvents.length === 0) {
            alert('Aucun vnement trouv pour la plage d\'annes slectionne.');
            return;
        }

        const doc = new jsPDF();
        const pageWidth = doc.internal.pageSize.width;
        const pageHeight = doc.internal.pageSize.height;

        // Dfinir la couleur du texte
        const textColor = [12, 26, 43];
        doc.setTextColor(textColor[0], textColor[1], textColor[2]);

        // En-tte
        doc.setFontSize(20);
        doc.setFont('courier', 'normal');
        doc.text('Liste des vnements', pageWidth / 2, 20, { align: 'center' });
        doc.setDrawColor(textColor[0], textColor[1], textColor[2]);;
        doc.setLineWidth(0.5); // paisseur de la ligne
        doc.line(10, 25, pageWidth - 10, 25); // Coordonnes de la ligne


        const imageX = 90; // Position X de l'image
        const imageY = 30; // Position Y de l'image
        const imageWidth = 25; // Largeur de l'image
        const imageHeight = 25; // Hauteur de l'image

        doc.addImage(imgpdf, 'WEBP', imageX, imageY, imageWidth, imageHeight);

        let y = imageY + imageHeight + 10; // Ajuster Y pour le texte aprs l'image

        // Ajouter une image


        filteredEvents.forEach((event, index) => {
            if (y > pageHeight - 40) { // Nouvelle page si ncessaire
                doc.addPage();
                y = 20;
                doc.text('Liste des vnements', pageWidth / 2, 20, { align: 'center' });
            }

            // Dessiner une puce
            doc.setFontSize(14);
            doc.setFont('helvetica', 'normal');
            doc.text('', 14, y); // La puce

            // Dessiner le texte de l'vnement
            doc.text(`${event.annee} : ${event.evenement}`, 20, y); // Dcalage pour la puce
            y += 10;
        });

        // Pied de page
        doc.setFontSize(10);
        doc.text('Gnr avec Quelle-Date.com', pageWidth / 2, pageHeight - 10, { align: 'center' });

        doc.save('fiche_revision.pdf');
    };

    return (
        <div className="revisionContainer">
            <div
                className="collapseHeader"
                onClick={() => setIsCollapsed(!isCollapsed)}
            >
                {isCollapsed ? 'Crer votre fiche de rvision' : 'Replier'}
            </div>
            <div className={`collapseContent ${!isCollapsed ? 'expanded' : ''}`}>
                <h2>Ma fiche de rvision</h2>
                <div>
                    <label>De l'anne ?</label>
                    <Select
                        options={yearOptions}
                        onChange={option => setStartYear(option ? option.value : null)}
                        placeholder="Slectionnez l'anne de dbut"
                    />
                </div>
                <div>
                    <label>A l'anne ?</label>
                    <Select
                        options={yearOptions}
                        onChange={option => setEndYear(option ? option.value : null)}
                        placeholder="Slectionnez l'anne de fin"
                    />
                </div>
                <button className="button" onClick={handleGeneratePDF}>Gnrer le PDF</button>
            </div>
        </div>
    );
};

export default PdfGenerator;